<template>
    <div class="detail ">
        <div class="routes full-width">
            <div class="container">
                <ul class="path">
                    <li><a href="/grade.html">商品中心</a></li>
                    <li v-for="(item,key) in GoodsCat"><a
                    :href="`/grade.html?limit=20&order=asc&type=3&page=1&token&cid=${key}`">{{item}}</a>
                    </li>
                </ul>
            </div>
        </div>
        <div class="good-info full-width">
            <div class="container clearfix">
                <div class="good-default-imgs fl">
                    <div class="big-img">
                        <zoom
                        :previewImg="preview | img"
                        :zoomImg="preview | img"></zoom>
                    </div>
                    <ul class="img-list">
                        <li v-for="(i,k) in details.product_images" :class="{marginNo:(k+1)%5===0,active:preview===i}"
                            @click="changePreview(i)"><img
                                :src="i | img"
                                alt=""></li>
                    </ul>
                </div>
                <div class="good-default-info fl">
                    <p class="info-tile">{{details.product_name}}</p>
                    <div class="price-code">
                        <p><span class="tile al"> 专 享 价  ： </span><span
                                class="price">{{details|exch(ex)}}</span> &nbsp;{{ex.unit}}</p>
                        <p><span class="tile al">品牌名称：</span>{{details.brand_name}}</p>
                        <p><span class="tile al">产品编号：</span>{{details.product_code}}</p>
                    </div>
                    <div class="spec">
                        <div class="p clearfix"><span class="tile ar fl">选择规格：</span>
                            <ul class="fl clearfix">
                                <li class="fl cursor"
                                    v-for="(i,k) in details.goodsSpec" @click="returnTO(i.product_id)"
                                    :class="{erabe:i.product_id === details.product_id}"
                                >
                                    <div class="a"
                                         :alt="i.colorname"
                                        >
                                        <img style="z-index: -1;" width="50" height="50" :src="i.default_img | img" alt="">
                                        <a>{{i.colorname?i.colorname:'默认'}}</a>
                                    </div>
                                </li>
                            </ul>
                        </div>
                    </div>
                    <div class="number">
                        <div class="num p clearfix">
                            <span class="tile ar fl">选择数量：</span>
                            <input type="button" value="-" :disabled="num<2" @click="setNum(-1)">
                            <input type="text" v-model="num">
                            <input type="button" value="+" @click="setNum(1)">
                        </div>
                    </div>
                    <div class="create-order">
                        <div class="order p">
                            <span class="tile ar fl">
                                    &nbsp;
                            </span>
                            <a class="btn buy"
                               :href="`usercenter.html#/order/Settlement?goods=${$route.params.id}`"
                            >立即购买</a>
                            <a class="btn cart" @click="addCart">加入购物车</a>
                            <a class="btn collect ac" @click="addCollect">
                                <img v-if="details.isCollect === 1" src="../assets/line-icon-collect-selected.png" alt="">
                                <img v-else src="../assets/line-icon-collect-defualt.png" alt="">
                                <span>收藏</span>
                            </a>
                        </div>
                    </div>
                    <div class="promise">
                        <div class="goods-promise p">
                            <span class="tile ar fl">
                                    服务承诺：
                            </span>
                            <img src="../assets/正.png" alt="">
                            <a class="promise-item">正品保障</a>
                            <img src="../assets/七.png" alt="">
                            <a class="promise-item">7天无理由退换货</a>
                            <img src="../assets/值.png" alt="">
                            <a class="promise-item">企业专享</a>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <detail :detail="details.product_info"></detail>
    </div>
</template>

<script>
    import zoom from '../pyg/zoom'
    import detail from '../components/detail'

    export default {
        name: "index",
        components: {
            zoom,
            detail
        },
        data() {
            return {
                testUrl: /http:\/\//i,
                details: {
                    product_info:''
                },
                ex: Object,
                num: 1,
                GoodsCat: Object,
                preview: ''
            }
        },
        created() {
            let id = this.$route.params.id;
            this.getGoodsInfo(id);
            this.getGoodsCat(id)
        },
        watch: {
            GoodsId(NewValue, OldValue) {
                this.getGoodsInfo(NewValue)
            }
        },
        computed: {
            GoodsId: {
                get() {
                    return this.$route.params.id
                }
            }
        },
        methods: {
            getGoodsCat(id) {
                this.yum('api/Product/getGoodsCat', {product_id: id}).then(res => {
                    if (res.data.status === 200) {
                        this.GoodsCat = res.data.data
                    }
                })
            },
            setNum(i) {
                this.num += i
            },
            returnTO(id) {
                this.$router.push(`/${id}`);
                this.$router.go(0)
            },
            getGoodsInfo(id) {
                this.yum('api/Product/getGoodsInfo', {product_id: id}).then(res => {
                    if (res.data.status === 200) {
                        this.details = res.data.data[0];
                        this.preview = res.data.data[0].default_img;
                        this.ex = {
                            exchange: res.data.exchange,
                            has_custom_price: res.data.has_custom_price,
                            unit: res.data.unit
                        };
                    } else {
                        alert('商品不存在')
                    }
                })
            },
            changePreview(i) {
                this.preview = i
            },
            addCart(){
                let data ={
                    product_id:this.$route.params.id,
                    status:1,
                    nums:1
                }
                this.yum('api/member/addCart',data).then(res=>{
                    if (res.data.status === 200){
                        this.bus.$emit('cart_notice')
                    }
                })
            },
            addCollect(){
                if (this.details.isCollect === 0) {
                    this.yum('api/member/collect',{product_id:this.$route.params.id}).then(res=>{
                        this.bus.$emit('collect_notice');
                        this.details.isCollect = 1
                    });
                }else{
                    this.yum('api/member/delCollect',{product_id:this.$route.params.id}).then(res=>{
                        this.details.isCollect = 0
                    })
                }
            }
        }
    }
</script>

<style scoped lang="stylus">
    .routes
        background #f8f8fa

    ul.path
        height 40px
        line-height 40px
        li
            display inline-block
            margin-right 10px
            &:last-child:after
                display none
            &:after
                display inline-block
                content ''
                background-size contain
                background-repeat no-repeat
                margin-left 10px
                width 8px
                height 11px
                background-image url("../assets/r.png")

    .good-info
        margin-top 14px
        margin-bottom 60px
        .good-default-imgs
            width 348px
            position relative
            .big-img
                position relative
                width 348px
                height 348px
                border 1px solid #dcdcdc
            .img-list
                display block
                width 348px
                margin-top 20px
                li
                    width 60px
                    height 60px
                    display inline-block
                    margin-right 12px
                    margin-bottom 12px
                    cursor pointer
                    &.marginNo
                        margin-right 0
                    &.active
                        border 1px solid #f07
                    &:last-child
                        margin-right 0
                    img
                        width 100%
                        height 100%
        .good-default-info
            width 780px
            margin-left 50px
            .info-tile
                font-size 16px
                line-height 32px
                font-weight bold
                color black
                text-indent 1em
            .price-code
                width 780px
                background-color #f2f2f2
                padding 20px 20px
                p
                    font-size 14px
                    color #333
                    line-height 30px
                    span.tile
                        display inline-block
                        min-width 80px
                    span.price
                        font-size 24px
                        color #ff0077
                        font-weight bold
            .spec
                width 780px
                padding 20px 10px
                div.a
                    position relative
                    width 100%
                    height 100%
                    display block
                    background-size cover
                    overflow hidden
                    img
                        position absolute
                        display block
                        top 0
                        left 0
                    a
                        color white
                        width 50px
                        height 50px
                        line-height 25px
                        display block
                        text-align center
                        font-size 13px
                        transform translateY(-100%)
                        background-color #ff0077
                        transition 0.3s
                    &:hover a
                        transform translateY(0)
            .number
                width 780px
                padding 10px 10px
                div.num
                    line-height 30px
                    input
                        border 1px solid #dcdcdc
                        background-color #f8f8fa
                        margin-right 3px
                        outline none
                        cursor pointer
                    input[type=button]
                        width 22px
                        height 30px
                    input[type=text]
                        width 50px
                        height 30px
                        text-align center
            .create-order
                width 780px
                padding 20px 10px
                div.p
                    height 40px
                    line-height 40px
                    a.btn
                        display inline-block
                        width 180px
                        height 40px
                        line-height 40px
                        text-align center
                        font-size 16px
                        font-weight bold
                        margin-right 20px
                        &.buy
                            background-color #ffe5f1
                            color #ff0077
                            border 1px solid #ff0077
                            &:hover
                                background-color #ffedf6
                                color #ff0077
                                border 1px solid #ff0077
                        &.cart
                            background-color #ff0077
                            color white
                            border 1px solid #ff0077
                            &:hover
                                background-color #ff3592
                                color white
                                border 1px solid #ff3592
                        &.collect
                            position relative
                            width 50px
                            font-size 14px
                            border 1px solid #cccccc
                            img
                                position absolute
                                right 15px
                                top 4px
                            span
                                vertical-align: middle
                                position: relative
                                color black
                                width: 50px
                                display: inline-block
                                transform: translateY(8px)
            .promise
                width 780px
                padding 0 10px
                div.p
                    height 20px
                    line-height 20px
                    img
                        margin-right 5px
                        transform translateY(-1px)
                    .promise-item
                        margin-right 40px
            p, div.p
                font-size 14px
                color #333
                line-height 50px
                span.tile
                    display inline-block
                    min-width 80px
                ul
                    max-width 400px
                li
                    width 50px
                    height 50px
                    margin-right 10px
                    margin-bottom 10px
                    border 1px solid #dcdcdc
                    &.erabe
                        border-color #ff0077
</style>
