<template>
    <div class="detail-main full-width">
        <div class="container clearfix">
            <div class="le-detail fl">
                <div class="le-tab-detail">
                    <ul>
                        <li :class="{active:active_tab === 0}" @click="active_tab = 0">商品详情</li>
                        <li :class="{active:active_tab === 1}" @click="active_tab = 1">配送时间</li>
                        <li :class="{active:active_tab === 2}" @click="active_tab = 2">退换货政策</li>
                    </ul>
                </div>
                <div class="le-box-detail" id="detail" v-html="details" v-if="active_tab===0">

                </div>
                <div class="le-box-detail" v-if="active_tab===1" v-html="ShipTime">

                </div>
                <div class="le-box-detail" v-if="active_tab===2" v-html="returnGood">

                </div>
            </div>
            <div class="ri-recommend fl" v-if="SimilarGoods.length>1">
                <p class="top-tile">猜你喜欢</p>
                <div class="cursor ri-recommend-item" v-for="(item,index) in SimilarGoods" :key="index">
                    <a target="_blank"
                       :href="`goods.html#/${item.product_id}`"><img :src="item.default_img|img" alt="">
                    </a>
                    <p class="tile"><a target="_blank"
                                       :href="`goods.html#/${item.product_id}`">{{item.product_name}}</a></p>
                    <p class="price bold">{{item|exch(ex)}}  <span style="font-size: 14px;color:black">{{ex.unit}}</span></p>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "detail",
        props: {
            detail: String
        },
        data() {
            return {
                active_tab: 0,
                SimilarGoods: [],
                ex: Object,
                ShipTime:'',
                returnGood:'',
            }
        },
        mounted() {
            this.getSimilarGoods();
            this.getShipTime();
            this.returnGoods();
            setTimeout(() => {
                this.fixedDetailImg()
            }, 100)
        },
        computed:{
          details(){
              return this.detail.replace(/\/ueditor1_4_3\/upload\/images\//g,'http://mapi.fufentong.com/ueditor1_4_3/upload/images/')
          }
        },
        methods: {
            fixedDetailImg() {
                let detail = document.getElementById("detail");
                let imgs = detail.getElementsByTagName('img');
                for (let index = 0; index < imgs.length; index++) {
                    imgs[index].style.width = '100%'
                }
            },
            getSimilarGoods() {
                let id = this.$route.params.id;
                this.yum('api/Product/getSimilarGoods', {product_id: id}).then(res => {
                    if (res.data.status === 200) {
                        this.ex = {
                            exchange: res.data.exchange,
                            has_custom_price: res.data.has_custom_price,
                            unit: res.data.unit
                        };
                        this.SimilarGoods = res.data.data;
                    }
                })
            },
            getShipTime(){
                this.yum('/api/article/getArticle',{articleId:14}).then(res=>{
                    if (res.data.status === 200){
                        this.ShipTime = res.data.data[0].content
                    }
                })
            },
            returnGoods(){
                this.yum('/api/article/getArticle',{articleId:18}).then(res=>{
                    if (res.data.status === 200){
                        this.returnGood = res.data.data[0].content
                    }
                })
            }
        }
    }
</script>

<style scoped lang="stylus">
    .le-detail
        width 868px
        .le-tab-detail
            height 40px
            background-color #f7f7f7
            ul
                display inline-block
                line-height 40px
                li
                    display inline-block
                    width 110px
                    height 40px
                    line-height 40px
                    text-align center
                    border 1px solid #f7f7f7
                    cursor pointer
                    &.active
                        color white
                        background-color #ff0077
                        border-color #ff0077
                    &:hover
                        border-color #ff0077
        .le-box-detail
            padding 60px
            position relative
            width 100%

    .ri-recommend
        width 260px
        /*border 1px solid #ccc*/
        /*padding 20px*/
        margin-left 50px
        p.top-tile
            font-size 16px
            font-weight bold
            padding 10px
            background-color #f7f7f7
        &-item
            width 260px
            margin-top 20px
            border 1px solid #ccc
            padding 20px
            img
                width 220px
            .tile
                margin-top 10px
                font-size 16px
                color black
                font-weight normal
                &:hover
                    color #ff0077
            .price
                font-size 18px
                color #ff0077
</style>