import Vue from 'vue'
import router from './router'
import App from './app.vue'
import MetaInfo from 'vue-meta-info'
import '../../assets/css/public.css'
import { createStore } from './vue.state.js'
import bus from '../../components/event-bus'
Vue.prototype.bus = bus;
import yum from '../../components/yum'
import stroage from '../../components/storage'
Vue.prototype.storage = stroage
Vue.filter('img', function ($url){
    if(/^http/.test($url)){
        return $url.replace('com/n0', 'com/img');
    }else if(/^\/.{4}\//.test($url)){
        // /2017/12/25/5a40b39bc9819.jpg
        return 'http://admin.fufentong.com/media/upload'+$url;
    }else{
        // /tmp/uploads/20181127/4410c3daa57707828e7e242aa97395fa.png
        return 'http://mapi.fufentong.com'+$url;
    }
});


Vue.filter('exch', function (item, ex) {
    let jifen = item.jifen;
    let exchange = ex.exchange;
    let num = parseInt(jifen) / parseInt(exchange);
    let show = (Math.round(num * 100) / 100);
    return exchange == 10 ? show.toFixed(2) : show
});
const store = createStore;
Vue.use(MetaInfo);
Vue.prototype.yum = yum;
new Vue({
    el: '#app',
    router,
    store,
    render: h => h(App)
});
